* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: transparent;
  width: 248px;
  height: 65px;
  line-height: 65px;
  display: inline-block;
  text-align: center;
  color: #000000;
  font-weight: 600;
  border: 2px solid #000000;
  text-decoration: none;
  cursor: pointer;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 70px;
}

.navigation.fixmenu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 150px;
  color: black;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.3);
}

.navigation__ul {
  list-style-type: none;
  display: flex;
}

.navigation__ul.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.navigation__li {
  margin-bottom: 40px;
  padding-left: 40px;
}

.navigation__a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 600;
}

.navigation__b {
  margin-top: 80px;
  padding-left: 40px;
}

.navigation__b_a {
  text-decoration: none;
  color: #FFFFFF;
  font-size: 14;
  font-weight: 400;
}

.navigation__b_a:before {
  content: '';
  background-image: url('./img//svg/ico-phone.svg');
  width: 15px;
  height: 15px;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
}

.navigation__b_p {
  color: #FFFFFF;
  text-decoration: underline;
  margin-top: 20px;
  text-align: center;
}

.mobile-menu {
  display: block;
  width: 30px;
  height: 16px;
  margin-top: 14px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  position: relative;
  outline: none;
  text-align: right;
}

.mobile-menu.closer {
  position: fixed;
  z-index: 10;
  outline: none;
  top: 20px;
  right: 20px;
  border: none;
  transition: 1s;
}

.mobile-menu.closer:before,
.mobile-menu.closer:after {
  content: '';
  display: block;
  min-width: 100%;
  position: absolute;
  border: 1px solid #ffff;
  outline: none;
  transform-origin: 62% 62%;
  transition: 0.5s;
}

.mobile-menu.closer:before {
  transform: rotate(-45deg);
}

.mobile-menu.closer:after {
  transform: rotate(45deg);
}

.mobile-menu:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  transform: translate(0, -100%);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-bottom: 2px solid #000000;
}

.mobile-menu:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 20px;
  transform: translate(0, -50%);
  border-bottom: 2px solid #000000;
}

