.closer-modal {
  position: absolute;
  top: 35px;
  right: 25px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
}

.closer-modal:before,
.closer-modal:after {
  content: '';
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  border-bottom: 1px solid #fff;
  transition: 0.4s;
}

.closer-modal:hover:before,
.closer-modal:hover:after {
  border-bottom: 2px solid #fff;
}

.closer-modal:before {
  transform: rotate(45deg);
}

.closer-modal:after {
  transform: rotate(-45deg);
}

